.container {
  position: relative;
  display: flex;
  flex-direction: column;
  -webkit-box-pack: justify;
  justify-content: space-between;
  width: 100%;
  max-width: 800px;
  color: rgb(45, 62, 80);
  backface-visibility: hidden;
  background-color: rgb(255, 255, 255);
  box-shadow: rgba(0, 0, 0, 0.5) 0px 0.5em 1.5em -0.5em;
  border-radius: 0;
  padding: 3em;
  border-width: 1px;
  border-style: solid;
  border-color: rgb(242, 245, 248);
  border-image: initial;
  border-radius: 0;
  transition: transform 0.5s ease;
}

.text-container {
  display: flex;
  width: 100%;
}

.text.left {
  width: 100%;
  height: 24em;
  display: flex;
  -webkit-box-flex: 1;
  flex-grow: 1;
  flex-direction: column;
  -webkit-box-pack: justify;
  justify-content: space-between;
  align-items: normal;
}

.text.right {
  width: 100%;
  display: flex;
  -webkit-box-flex: 1;
  flex-grow: 1;
  flex-direction: column;
  -webkit-box-pack: justify;
  justify-content: space-between;
  align-items: flex-end;
}

.invoice-title {
  font-size: 2rem;
  line-height: 1.3125;
}

.company-info {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: 0px auto;
}

.compnay-input-container {
  width: 100%;
  display: flex;
  -webkit-box-pack: start;
  justify-content: flex-start;
}

tbody {
  background-color: white;
}

.add-more-btn {
  box-sizing: border-box;
  width: 100%;
  max-height: 0px;
  display: flex;
  -webkit-box-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  align-items: center;
  background-color: #33aeae;
  cursor: pointer;
  overflow: hidden;
  transition: max-height 0.3s;
}

.table-container:hover .add-more-btn {
  max-height: 3em;
}

.add-more-btn span {
  color: rgb(255, 255, 255);
  font-size: 1em;
  font-weight: 600;
  padding: 0.5em 0px;
}

.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-sizing: content-box;
  width: 100%;
  perspective: 500px;
  background-color: rgba(255, 255, 255, 0);
  margin: 0px auto;
  padding: 6em 0px;
  overflow: hidden;
}

.logo-container {
  position: relative;
  width: 60%;
  max-width: 60%;
  display: flex;
  -moz-box-pack: center;
  justify-content: center;
  align-items: center;
  -moz-box-align: center;
  margin-left: auto;
  overflow: hidden;
  transform: scale(1);
  transition: transform 0.3s ease 0s;
  cursor: pointer;
}

.logo-container::after {
  content: "";
  display: block;
  padding-top: 56.25%;
  cursor: pointer;
}

.logo-container:hover {
  transform: scale(0.9);
}

.upload-text {
  font-size: 1em;
  font-weight: 600;
  text-align: center;
  width: 50%;
}

.logo-container .upload-button {
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: pointer;
  font-size: 10em;
  left: 0px;
  top: 0px;
  position: absolute;
}

.download-container {
  margin-bottom: 2rem;
  max-width: 800px;
  width: 100%;
  text-align: right;
}
.download-btn {
  background: #33aeae;
  color: #fff;
  font-weight: 600;
  padding: 0.7rem;
  border-radius: 4px;
}

.heading {
  float: left;
}

img {
  max-width: 150px;
  max-height: 150px;
  /*filter: grayscale(100%);*/
}

#invoiceDropdown {
  position: relative;
  z-index: 3;
}

#upldTXT {
  position: absolute;
  z-index: -1;
}
