.tableBack {
  position: absolute;
  height: 100vh;
  width: 100%;
  background: #f3f5f9;
  z-index: 11;
}
.lds-facebook {
  top: 50%;
  left: 50%;
  transform: translate(-250%, -150%);
  display: inline-block;
  position: absolute;
  width: 80px;
  height: 80px;
}
.lds-facebook div {
  display: inline-block;
  position: absolute;
  left: 8px;
  width: 16px;
  background: #cef;
  animation: lds-facebook 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;
}
.lds-facebook div:nth-child(1) {
  left: 8px;
  animation-delay: -0.24s;
  background: #008a39;
}
.lds-facebook div:nth-child(2) {
  left: 32px;
  animation-delay: -0.12s;
  background: #f6b60e;
}
.lds-facebook div:nth-child(3) {
  left: 56px;
  animation-delay: 0;
  background: #9c1418;
}
@keyframes lds-facebook {
  0% {
    top: 8px;
    height: 64px;
  }
  50%, 100% {
    top: 24px;
    height: 32px;
  }
}
